body{
  font-family: "karla" sans-serif;
  margin: 0;
}
*{
  box-sizing: border-box;
}
.header{
  display: flex;
  align-items: center;
  height: 65px;
  background: linear-gradient(90deg,#672280 1.18%,  #A626D3 100%);
  color: white;
  padding: 15px;

}
.header-image{
  height: 100%;
 margin-right: 6px;
}
.header-title{
  font-size: 20px;
  margin-right: auto;
}
.header-project{
  font-size: 14px;
  font-weight: 600;
}

main{
  padding: 36px;
}
.form{
  /* display: grid;
  grid-template:20px 20px/ 1fr 1fr; */
  gap: 17px;
  display: inline-block;
  
}
.form-input{
  border-radius: 5px;
  text-indent: 5px;
  width: 47%;
  height: 40px; 
  
}
.form-button{
  border-radius: 5px;
  border: 1px solid #D5D4D8;
  background: linear-gradient(90deg,#672280 1.18%,  #A626D3 100%);
  height: 50px;
  color: white;
  border: none;
  font-weight: bold;
  font-size: large;
  cursor: pointer;
  width: 100% ;
  margin-top: 30px;

}

.div-img{
 max-width: 100% ;
 max-height: 100%;
 margin-top: 50px;
}

/* ============== */
.box{
  display: inline-block;
  width: 150px;
  height: 150px;
  border: 1px solid black;
  margin-left: 5px;
  margin-top: 5px;
  border-radius: 5px;
}
.mem{
  position: relative;
  width: 95%;
  
}
.top-text{
  position: absolute;
  top:20%;
  left: 50%;
  transform: translateX(-50%);
  color:black;
  /* -webkit-text-stroke: 1px black; */
  font-size:60px;
  font-weight: 900;
  -webkit-text-stroke: 2px black;
  color: white;
}
.bottom-text{
  position: absolute;
  top:80%;
  left: 50%;
  transform: translateX(-50%);
  font-size:60px;
  font-weight: 900;
  -webkit-text-stroke: 2px black;
  color: white;
  
}